@import url(https://fonts.googleapis.com/css2?family=Josefin+Slab:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'Josefin Slab', sans-serif;
  background: radial-gradient( #595557 100%, #393A3E 100%);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  font-family: 'Josefin Slab', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

a:link, a:visited, a:active {
    text-decoration:none;
    color: white;
  }

.Header-nav{
    background-color: #292929;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px;
    height: 50px;
}

.Nav-container {
    font-size: 1.5em;
    width: 60%;
    display: flex;
    justify-content: space-around;
}

.Image-nav img{
    height: 50px;
}
.Resumen {
    color: white;
    font-size: 1.5em;
    padding: 0 100px;
}

.Resumen h3 {
    margin-top: 40px;
    border-bottom: 1px solid white;
}

table {
    border-collapse: collapse;
    width: 100%;
}

td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
  }
.Footer{
    background-color: #292929;
    height: 50px;
    display: flex;
    align-items: center;
    color: white;
    justify-content: center;
    font-weight: normal;
    bottom: 0;
    width: 100%;
}
.Banner-pizza{
    background-image: url(/pizzaShop/static/media/Table.8d5e271d.svg);
    height: 100px;
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.Container-crearPizza h1 {
    color: white;
    display: flex;
    justify-content: center;
    width: 25%;
    font-weight: normal;
    font-size: 3em;
    margin-top: 0;
    padding: 20px;
    border-bottom: 1px solid white;
}

.name-input{
    color:white;
    display: flex;
    flex-direction: column;
    width: 40%;
    font-size: 1.5em;
    margin: 0 45px;
}

.Container-ingredientes {
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 20px;
    color:#292929;
    display: flex;
    flex-direction: column;
    font-size: 1.5em;
    align-items: center;
    margin: 20px 40px;
    height: 450px;
}

.Container-ingredientes h2 {
    font-weight: normal;
    font-size: 1.5em;
    margin: 0 0 20 0;
    padding-bottom: 10px;
}

.Container-products{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    font-size: 1.1em;
}

.Container-products label {
    width: 30%;
    margin:10px;
}

.Input-PizzaName, .Input-Name, .Input-Phone{
    border-radius: 12px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
    height: 30px;
    border-style: none;
}

.container-last {
    display: flex;
    flex-direction: row-reverse;
}

.container-lastform {
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 400px;
    margin: 40px;
    padding: 40px;
    width: 50%;
}
.container-lastform label, p {
    font-size: 1.2em;
    width: 75%;
    margin:5px 0;

}

.container-lastform h4 {
    font-size: 1.5em;
}
.container-lastform input {
    width: 100%;
    margin:2px;
}
.button-form {
    width: 30%;
    font-size: 1.1em;
    background-color: #292929;
    border: 1px solid #292929;
    border-radius: 15px;
    color: white;
    height: 50px;
    margin: 15px 0;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.Banner-tracking{
    background-image: url(/pizzaShop/static/media/Table.8d5e271d.svg);
    height: 80px;
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.table-tracking{
    color:white;
    font-size: 1.3em;
    margin: 40px;
    width: 95%;
}

h1 {
    color: white;
    display: flex;
    justify-content: center;
    width: 25%;
    font-weight: normal;
    font-size: 3em;
    margin-top: 0;
    padding: 20px;
    border-bottom: 1px solid white;
}
